body {
  /* margin: 0;
  padding: 0; */
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
                               
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#logo,
#logo:visited,
#logo:active {
  color: aliceblue;
  text-decoration: none;
}
#logo:hover {
  -webkit-filter: brightness(85%);
          filter: brightness(85%);
}
ul#menu li {
  display:inline;
  margin-left: 0.5em;

}
ul#menu{
  margin: 0;
}
@media screen and (max-width: 600px) {
  ul#menu li {
  display: block;
  margin-top: 0.30em;
  padding: 0 !important;
  }
  .rowr{
    float: right;
  }
  
}
.home {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  height: 100vh;
  background-image: url(/popworld_updated/static/media/Landing_Page_Image.ab17526e.jpg);
  background-position-x: center;
  background-position-y: center;
  background-size: cover;
  background-repeat: no-repeat;
  font-size: 20px;
}

.start_button {
  color: black;
  background-color: black;
  width: 200px;
  height: 50px;
  font-size: 18px;
}

#developer-header {
  background-color: rgb(43, 60, 109);
  color: white;
  font-size: 25px;
  text-align: center;
}

#developer-card {
  margin-top: 4rem;
  margin-bottom: 3rem;
  -webkit-transition: 300ms;
  transition: 300ms;
}

#developer-card:hover {
  box-shadow: -1px 9px 40px -12px rgba(0, 0, 0, 0.75);
  -webkit-transition: 300ms;
  transition: 300ms;
}

footer {
  position: -webkit-sticky;
  position: sticky;
  text-align: center;
  padding: 1rem;
  background: #111111;
  color: #dddddd;
  width: 100%;
}

#about-header {
  background-color: rgb(43, 60, 109);
  color: white;
  font-size: 25px;
  text-align: center;
}

#update-header-1 {
  background-color: rgb(107, 43, 109);
  color: white;
  font-size: 25px;
}

#update-header-2 {
  background-color: rgb(43, 58, 109);
  color: white;
  font-size: 25px;
}

#update-header-3 {
  background-color: rgb(116, 36, 36);
  color: white;
  font-size: 25px;
}

#about-card {
  margin-top: 6rem;
  -webkit-transition: 300ms;
  transition: 300ms;
}

#about-card:hover {
  box-shadow: -1px 9px 40px -12px rgba(0, 0, 0, 0.75);
  -webkit-transition: 300ms;
  transition: 300ms;
}

#update-cards {
  margin-bottom: 2rem;
  margin-top: 3rem;
}

#update:hover {
  box-shadow: -1px 9px 40px -12px rgba(0, 0, 0, 0.75);
  -webkit-transition: 300ms;
  transition: 300ms;
}

#profile-pic {
  width: 50%;
  height: 20rem;
  border-radius: 50%;
  position: relative;
  left: 20%;
  top: 2%;
}

#professor-cards {
  margin-bottom: 2rem;
  margin-top: 5rem;
}

#professor:hover {
  box-shadow: -1px 9px 40px -12px rgba(0, 0, 0, 0.75);
  -webkit-transition: 300ms;
  transition: 300ms;
}

